import { Component, ElementRef, forwardRef, Inject, Injector, NgModule, Optional, Renderer2, ViewEncapsulation } from '@angular/core';
import { CommonModule } from '@angular/common';

import { OSharedModule } from '../../../shared';
import { OListItemComponent } from '../list-item/o-list-item.component';
import { OListItemCardRenderer } from './o-list-item-card-renderer.class';

export const DEFAULT_INPUTS_O_LIST_ITEM_CARD = [
  ...OListItemCardRenderer.DEFAULT_INPUTS_O_CARD_RENDERER
];

export const DEFAULT_OUTPUTS_O_LIST_ITEM_CARD = [
  ...OListItemCardRenderer.DEFAULT_OUTPUTS_O_CARD_RENDERER
];

@Component({
  moduleId: module.id,
  selector: 'o-list-item-card',
  template: `
    <mat-card fxLayout="column" fxLayoutAlign="center center">
      <mat-card-title-group>
        <img *ngIf="showImage" src="{{ image }}" [class.mat-card-sm-image]="compareListHeight('small')" [class.mat-card-md-image]="compareListHeight('medium')"
          [class.mat-card-lg-image]="compareListHeight('large')">
        <mat-card-title *ngIf="title !== undefined"> {{ title }}</mat-card-title>
        <mat-card-subtitle *ngIf="subtitle !== undefined"> {{ subtitle }}</mat-card-subtitle>
      </mat-card-title-group>
      <mat-card-actions>
        <button type="button" mat-button *ngIf="action1Text !== undefined" (click)="onAction1ButtonClick($event)">{{ action1Text }}</button>
        <button type="button" mat-button *ngIf="action2Text !== undefined" (click)="onAction2ButtonClick($event)">{{ action2Text }}</button>
      </mat-card-actions>
    </mat-card>
  `,
  styles: [`
    .mat-list .mat-list-item.o-card-item,.mat-list .mat-list-item.o-card-item .mat-list-item-content{height:auto}.o-list-item-card{padding:8px 0}.o-list-item-card mat-card,.o-list-item-card mat-card-actions,.o-list-item-card mat-card-title-group{width:100%}
  `],
  inputs: DEFAULT_INPUTS_O_LIST_ITEM_CARD,
  outputs: DEFAULT_OUTPUTS_O_LIST_ITEM_CARD,
  encapsulation: ViewEncapsulation.None,
  host: {
    '[class.o-custom-list-item]': 'true',
    '[class.o-list-item-card]': 'true'
  }
})
export class OListItemCardComponent extends OListItemCardRenderer {

  constructor(
    elRef: ElementRef,
    _renderer: Renderer2,
    _injector: Injector,
    @Optional() @Inject(forwardRef(() => OListItemComponent)) _listItem: OListItemComponent
  ) {
    super(elRef, _renderer, _injector, _listItem);
  }

  ngAfterViewInit() {
    this.modifyMatListItemElement();
  }

}

@NgModule({
  declarations: [OListItemCardComponent],
  imports: [CommonModule, OSharedModule],
  exports: [OListItemCardComponent]
})
export class OListItemCardModule { }
